import Vue from 'vue';
import VueResource from 'vue-resource';
import App from './App';
import 'reset.css';

Vue.use(VueResource);

Vue.filter('thousands', require('./filters/thousands.js').default);

/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: { App },
});



