<template>
  <div class="dialog-wrap">
    <div class="dialog-header">
      <slot name="header">
        <h3 class="dialog-title" :class="[titlePosition]">{{ title }}</h3>
      </slot>
    </div>
    <div class="dialog-body">
      <slot name="body"></slot>
    </div>
    <div class="dialog-footer">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
  export default {
    components: {},

    props: {
      title: { type: String, default: '' },
      titlePosition: { type: String, default: 'center' },
    },

    data() {
      return {};
    },

    computed: {},

    methods: {},

    ready() {},
  };
</script>

<style scoped>
  .dialog-wrap {
    background: rgba(0, 0, 0, .2);
    border-radius: 5px;
    box-shadow: 2px 2px 8px rgba(0,0,0,.5);
  }

  .dialog-header {
    border-bottom: 1px solid rgba(255,255,255,.1);
    box-shadow: 0 -1px 0 0 rgba(0,0,0,.2) inset;
  }

  .dialog-title {
    margin: 0;
    padding: 10px 15px;
    text-align: center;
    color: #28c6df;
  }
  .dialog-title.left {
    text-align: left;
  }
  .dialog-title.right {
    text-align: right;
  }

  .dialog-body {
  }

  .dialog-footer {

  }
</style>



