export default function (value, digits = 1) {
  const val = parseFloat(value);
  if (isNaN(val)) {
    return '';
  }

  let result = '';
  const num = `${Math.round(val * Math.pow(10, digits)) / Math.pow(10, digits)}`;
  const negative = val < 0;
  /* eslint-disable prefer-const */
  let [perfix, suffix] = (negative ? num.slice(1) : num).split('.');

  while (perfix.length > 3) {
    result = `,${perfix.slice(-3)}${result}`;
    perfix = perfix.slice(0, perfix.length - 3);
  }

  result = perfix + result;

  if (suffix) {
    result += `.${suffix}`;
  }

  if (negative) {
    result = `-${result}`;
  }

  return result;
}



