<template>
  <div class="data-box">
    <h3 class="value">
      {{ value | thousands }}
      <span class="unit" v-if="valueUnit">{{ valueUnit }}</span>
    </h3>
    <!-- <h5 class="percent" :class="{ decline: percent < 0 }">
      {{ Math.abs(percent) | thousands }}<span class="unit">%</span>
    </h5> -->
  </div>
</template>

<script>
  export default {
    props: {
      value: { type: Number },
      percent: { type: Number },
      valueUnit: { type: String, default: '' },
    },
  };
</script>

<style scoped>
.data-box {
  padding: 1em;
  text-align: center;
}

.value {
  font-size: 20px;
  line-height: 3;
}
.unit {
  opacity: .5;
}

.percent {
  color: #28c6df;
  line-height: 2;
}
.percent.decline {
  color: #FD9F2A;
}
.percent::before {
  content: "";
  display: inline-block;
  border-bottom: solid .6em;
  border-left: solid .4em transparent;
  border-right: solid .4em transparent;
  margin-right: .2em;
}
.percent.decline::before {
  border-top: solid .6em;
  border-bottom: none;
}
</style>



