<template>
  <component :is="currentPage" transition="fade" transition-mode="out-in">
  </component>
</template>

<script>
import login from './pages/login.vue';
import main from './pages/main.vue';

export default {
  components: {
    login,
    main,
  },

  data() {
    return {
      currentPage: '',
    };
  },

  ready() {
    const username = sessionStorage.getItem('jike-dashboard-user');
    if (username) {
      this.currentPage = 'main';
    } else {
      this.currentPage = 'login';
    }
  },

  events: {
    'login-success'(username) {
      sessionStorage.setItem('jike-dashboard-user', username);
      this.currentPage = 'main';
    },
    'logout'() {
      sessionStorage.removeItem('jike-dashboard-user');
      this.currentPage = 'login';
    },
  },
};
</script>

<style>
html{
  /*IE scrollbar*/
  scrollbar-arrow-color: #3397b3;
  scrollbar-face-color: #3397b3;
  scrollbar-3dlight-color: #3397b3;
  scrollbar-highlight-color: #3397b3;
  scrollbar-shadow-color: #3397b3;
  scrollbar-darkshadow-color: #0a0a0a;
  scrollbar-track-color: #324550;
  scrollbar-base-color:#121212;
}
/*chrome scrollbar*/
::-webkit-scrollbar {width: 8px;height:8px;}
::-webkit-scrollbar-track {-webkit-box-shadow: inset 0 0 6px #28c6de;-webkit-border-radius: 6px;border-radius: 6px;}
::-webkit-scrollbar-track-piece {background: #314552;}
::-webkit-scrollbar-thumb {-webkit-border-radius:6px;border-radius: 6px;background: #28c6de;-webkit-box-shadow: inset 0 0 6px #28c6de;}
::-webkit-scrollbar-thumb:window-inactive {background: rgba(31, 67, 85, 0.4);}
::-webkit-scrollbar-corner  {background:#314552;}

body {
  font-size: 14px !important;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif !important;
  color: #fff;
  background: #075760 url(assets/bg.svg) no-repeat fixed top center/cover;
}
input, button {
  color: #fff;
}

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
*:before,
*:after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.btn {
  color: #28c6df;
  border: 1px solid #28c6df;
  background: rgba(40,198,223,.2);
  cursor: pointer;
  outline: none;
}
.btn:hover {
  background: rgba(40,198,223,.4);
}
.btn[disabled] {
  border-color: rgba(255,255,255,.2);
  background: rgba(255,255,255,.1);
}


.fade-transition {
  transition: opacity .6s ease;
  opacity: 1;
}
.fade-enter, .fade-leave {
  opacity: 0;
}
</style>



