<template>
  <div class="login-box-wrap">
    <dialog title="登 录" class="login-box" :class="{ shake: loginFailed }">
      <div slot="header" class="login-header"></div>
      <div slot="body" class="login-box-content">
        <input type="text" v-model="username" placeholder="用户名">
        <input type="password" v-model="password" placeholder="密码">
        <button class="btn" @click="login" :disabled="submitting">
          <span v-if="!submitting">登 录</span>
          <spinner v-else size="2em" border-width=".3em"></spinner>
        </button>
      </div>
    </dialog>
  </div>
</template>

<script>
  import dialog from '../components/dialog.vue';
  import spinner from '../components/spinner.vue';
  const USERS = require('../../data/users.json');

  export default {
    components: {
      dialog,
      spinner,
    },

    props: [],

    data() {
      return {
        username: '',
        password: '',
        loginFailed: false,
        submitting: false,
      };
    },

    computed: {},

    methods: {
      login() {
        this.submitting = true;

        setTimeout(() => {
          const success = USERS.some(
            (user) => user.username === this.username && user.password === this.password
          );

          if (success) {
            this.$dispatch('login-success', this.username);
          } else {
            this.loginFailed = true;
            setTimeout(() => (this.loginFailed = false), 1000);
          }

          this.submitting = false;
        }, 600);
      },
    },

    ready() {},
  };
</script>

<style scoped>
.login-box-wrap {
  font-size: 16px;
  position: fixed;
  width: 400px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 500px) {
  .login-box-wrap {
    width: 100%;
    left: 0;
    transform: translate(0, -50%);
  }
}
.login-box {
  animation-duration: 1s;
}

.login-box-content {
  padding: 2em 3em;
}

.login-header {
  height: 64px;
  background: url(../assets/jike-icon.png) center center no-repeat;
  background-size: 48px;
}

input {
  display: block;
  width: 100%;
  padding: 1em 2em;
  margin-bottom: 2em;
  border: 1px solid #28c6df;
  border-radius: 2em;
  background: transparent;
  outline: none;
}

button {
  display: block;
  width: 100%;
  padding: 1em;
  margin-bottom: 2em;
  border-radius: 2em;
}

.spinner {
  margin: -0.5em 0;
}

@keyframes shake {
  10%, 30%, 50%, 70%, 90% {
    transform: translate(-10px, 0);
  }

  20%, 40%, 60%, 80% {
    transform: translate(10px, 0);
  }
}

.shake {
  animation-name: shake;
}
</style>



