<template>
  <div class="main-page">
    <div class="topbar">
      <div class="logo"></div>
      <h1 class="app-title">Dashboard</h1>
      <div class="user-info">
        {{ username }}
        <button type="button" name="button" class="btn btn-logout" @click="$dispatch('logout')">退出</button>
      </div>
    </div>

    <div v-if="loading" class="loading-wrap">
      <spinner></spinner>
    </div>
    <div v-else class="main-body">
      <div v-for="data in dataList" class="data-box-wrap" transition="fade" stagger="1000">
        <dialog :title="data.title" title-position="left">
          <data-box
            slot="body"
            :value="data.value"
            :value-unit="data.unit"
            :percent="data.percent">
          </data-box>
        </dialog>
      </div>

      <p class="update-time">更新时间: {{ updateTime }}</p>
    </div>
  </div>
</template>

<script>
  import dialog from '../components/dialog.vue';
  import spinner from '../components/spinner.vue';
  import dataBox from '../components/data-box.vue';
  const DATA = require('../../data/main.json');

  export default {
    components: {
      dialog,
      spinner,
      dataBox,
    },

    props: [],

    data() {
      return {
        loading: true,
        username: sessionStorage.getItem('jike-dashboard-user'),
        updateTime: DATA.updateTime,
        dataList: DATA.dataList,
      };
    },

    computed: {},

    methods: {},

    ready() {
      // this.$http.get('main.json')
      //   .then(({ data }) => {
      //     this.statistics = data;
      //   });
      setTimeout(() => {
        this.loading = false;
      }, 600);
    },
  };
</script>

<style scoped>
.topbar {
  background: rgba(0,0,0,.7);
  height: 50px;
}
.logo {
  float: left;
  width: 40px;
  height: 40px;
  margin: 5px 10px;
  background: url(../assets/jike-icon.png) no-repeat;
  background-size: contain;
}
.app-title {
  float: left;
  line-height: 50px;
  font-size: 18px;
  font-weight: bold;
}
.user-info {
  float: right;
  line-height: 50px;
  margin: 0 1em;
  color: #28c6df;
}
.btn-logout {
  margin-left: 1em;
  padding: .5em 1em;
  border-radius: 3px;
}

.loading-wrap {
  text-align: center;
  padding: 2em;
}
.main-body {
  padding: 1em .5em;
  overflow: hidden;
}
.data-box-wrap {
  float: left;
  width: 33.33333333333333%;
  padding: .5em 1em;
}
@media (max-width: 500px) {
  .data-box-wrap {
    float: none;
    width: 100%;
  }
}

.update-time {
  text-align: right;
  color: #fff;
  opacity: .4;
  padding: 1em;
  clear: both;
}
</style>



